            <div class="page-artical-msg">
                <P>&nbsp&nbsp在Vue源码里，尤大神采用了Flow作为静态类型检查，Flow是facebook出品的静态类型检查工具。为什么要用Flow？</P>
                <p>&nbsp&nbsp众所周知，JavaScript是弱类型的语言。</p>
                <p>&nbsp&nbsp所谓弱类型指的是定义变量时，不需要什么类型，在程序运行过程中会自动判断类型，如果一个语言可以隐式转换它的所有类型，那么它的变量、表达式等在参与运算时，即使类型不正确，也能通过隐式转换来得到正确地类型，这对使用者而言，就好像所有类型都能进行所有运算一样，所以Javascript被称作弱类型。</p>
